.services {
  padding: 165px 0 183px;
  overflow: hidden;
  background-image: url(../images/services-bcg.png);
  background-position: right;
  background-repeat: no-repeat;
  @include media("<=1080px") {
    padding: 100px 0 120px;
  }
    @include media("<=810px") {
      padding: 80px 0;
    }
}

.services__inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  column-gap: 30px;
  row-gap: 185px;
  padding-top: 62px;
  @include media("<=1080px") {
    row-gap: 120px;
  }
    @include media("<=1000px") {
      flex-direction: column;
      row-gap: 100px;
    }
        @include media("<=430px") {
          row-gap: 60px;
        }
}

.services__item {
  display: flex;
  column-gap: 71px; 
  flex: 0 1 44%;
  align-items: flex-start;
  @include media("<=1000px") {
    flex: 0 1 100%;
    column-gap: 50px;
  }
    @include media("<=430px") {
      column-gap: 30px;
    }
}

.services__icon {
  max-width: 115px;
  width: 100%;
  aspect-ratio: 1 / 1;
  display: flex;
  align-items: center;
  justify-content: center;
  
  position: relative;
  @include media("<=430px") {
    max-width: 80px;
  }
  img {
    width: 50px;
    display: block;
    position: relative;
    @include media("<=430px") {
      width: 30px;
    }
  }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
      background: #A90A3D;
      filter: blur(25px);
  }
}

.services__title {
  font-size: 40px;
  font-weight: 700;
  font-family: "Hind", sans-serif;
  margin-bottom: 31px;
  @include media("<=1280px") {
    font-size: 32px;
  }
    @include media("<=430px") {
      font-size: 24px;
      margin-bottom: 20px;
    }
}

.services__text {
  color: rgba(255, 255, 255, 0.80);
    font-size: 24px;
    font-weight: 500;
    line-height: 40px;
    @include media("<=1280px") {
      font-size: 20px;
      line-height: 1.2;
    }
    @include media("<=430px") {
      font-size: 18px;
    }
}