.footer {
  padding: 135px 0 192px;
  position: relative;
  background-image: url(../images/footer-bcg.png);
    background-position: right;
    background-repeat: no-repeat;
  @include media("<=810px") {
    background-size: cover;
    background-position: center;
  }

  @include media("<=430px") {
    padding: 30px 0 60px;
  }

}

.footer__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 81px;
 column-gap: 124px;
  @include media("<=810px") {
    flex-direction: column;
    align-items: center;
    row-gap: 50px;
  }
}







