body {
  margin: 0;
  font-family: "Montserrat", sans-serif;
  background-color: #030D19;
  color: #ffffff;
  position: relative;
  @include media("<=810px") {
    
  }
  
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

.container {
  width: 100%;
  max-width: 1780px;
  padding: 0 15px;
  margin: 0 auto;
}

.wrapper {
  position: relative;
  overflow: hidden;
}

.section__title {
  font-family: "Hind", sans-serif;
  color: #fff;
  text-align: center;
  font-size: 48px;
  font-weight: 500;
  position: relative;
  z-index: 4;
  @include media("<=810px") {
    font-size: 42px;
  }
  @include media("<=430px") {
    font-size: 30px;
  }
  span {
    display: block;
    font-size: 80px;
    @include media("<=810px") {
      font-size: 60px;
    }
    @include media("<=430px") {
      font-size:34px;
    }
  }
}

//BURGER BUTTON//
.burger__button {
  display: none;

  @include media("<=810px") {
    display: block;
    position: relative;
    width: 3rem;
    height: 1.525rem;
    z-index: 20;
  } 
}

  .line, 
  .line-top, 
  .line-bottom {
    content: '';
    position: absolute;
    height: 0.125rem;
    width: 100%;
    left: 0;
    background-color: #ffffff;
    transition: all 200ms ease-in-out;
    opacity: 1;
    
  }

    
    
    .line-mid {
      top: calc(50% - 0.0625rem);
    }
        .line-top {
          top: 0;
        }
    
        .line-bottom {
          bottom: 0;
        }
    .burger__open .line-top {
      top: calc(50% - 0.0625rem);
      transform: rotate(-45deg);
    }
  
    .burger__open .line-bottom {
      bottom: calc(50% - 0.0625rem);
      transform: rotate(45deg);
    }
  
    .burger__open .line-mid {
      left: 50%;
      width: 0;
    }

    .burger__open .header__menu {
      left: 0;
    }
 
