.plans {
  padding-top: 115px;
  @include media("<=1280px") {
    padding-top: 80px;
  }
}

.bottom__title {
  font-family: "Hind", sans-serif;
    color: #fff;
    text-align: center;
    font-size: 60px;
    font-weight: 500;
    position: relative;
    @include media("<=1280px") {
      font-size: 40px;
    }
        @include media("<=1080px") {
          font-size: 32px;
        }
}

.plans__inner {
  padding-top: 85px;
  display: flex;
  justify-content: space-around;
  column-gap: 30px;
  padding-bottom: 41px;
  @include media("<=810px") {
    flex-direction: column;
    align-items: center;
    row-gap: 70px;
  }
}

.plans__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px 10px 0;
  border-radius: 20px;
    background:  #030D19;
    box-shadow: 0 0 55px #F7084C;
    max-width: 466px;
    width: 100%;
    height: 379px;
    @include media("<=1080px") {
      height: 320px;
    }
}

.plans__icon {
  max-width: 100px;
  margin-bottom: 16px;
  img {
    width: 100%;
    display: block;
  }
}

.plans__text {
  font-size: 24px;
    font-weight: 500;
    line-height: 40px;
    text-align: center;
    max-width: 401px;
    @include media("<=1080px") {
      font-size: 20px;
    }
}