.hero {
  padding: 134px 0 95px;
  position: relative;
  overflow: hidden;
  @include media("<=1440px") {
    padding: 120px 0 90px;
  }
    @include media("<=1280px") {
      padding-top: 100px;
    }
    @include media("<=1080px") {
      padding-top: 80px;
    }
  @include media("<=810px") {
    padding-top: 60px;
    padding-bottom: 50px;
  }
  @include media("<=430px") {
    padding-bottom: 30px;
  }
  @include media("<=375px") {
  }
  @include media("<=320px") {
  }
  
}

.hero__inner {
  width: 100%;
  margin: 0 auto;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  column-gap: 48px;
  @include media("<=1280px") {
    
  }
  @include media("<=1080px") {
    
  }
  @include media("<=810px") {
    flex-direction: column;
    align-items: center;
    
  }
  @include media("<=430px") {
    
  }
    &::before {
      content: '';
      position: absolute;
      top: 10%;
      right: 15%;
      max-width: 800px;
      width: 100%;
      aspect-ratio: 1 / 1;
      border-radius: 50%;
      background: radial-gradient(circle, rgba(169, 10, 61, 1) 0%, rgba(169, 10, 61, 0) 100%);
      filter: blur(25px);
      opacity: .6;
      @include media("<=1080px") {
        right: 0;
        max-width: 600px;
      }
            @include media("<=810px") {
              max-width: 800px;
              right: -25%;
            }
    }
}

.hero__content {
  max-width: 1005px;
  flex: 0 1 100%;
  display: flex;
  flex-direction: column;
  z-index: 3;
  @include media("<=1280px") {
    max-width: 755px;
  }
    @include media("<=810px") {
      align-items: center;
    }
}

.hero__title {
  color: #fff;
  font-size: 80px;
  font-weight: 500;
  font-family: "Hind", sans-serif;
  @include media("<=1440px") {
    font-size: 72px;
  }
  @include media("<=1280px") {
    font-size: 62px;
  }
  @include media("<=1080px") {
    font-size: 54px;
    
  }
  @include media("<=810px") {
    font-size: 64px;
  }
  @include media("<=430px") {
    font-size: 32px;
   
  }

  @include media("<=350px") {
    font-size: 30px;
  }
}

.hero__text {
  margin-top: 31px;
  margin-bottom: 57px;
  color: #FFFFFF;
    font-size: 20px;
    line-height: 1.28;
opacity: .8;
@include media("<=1280px") {
 margin-bottom: 75px;
}
    @include media("<=1080px") {
      font-size: 18px;
      max-width: 627px;
      margin-bottom: 155px;
    }
    
      @include media("<=810px") {
        font-size: 20px;
        margin-bottom: 40px;
        max-width: 100%;
      }
        @include media("<=430px") {
          font-size: 15px;
          margin-top: 14px;
          margin-bottom: 38px;
        }
        @include media("<=350px") {
          font-size: 12px;
        }
}


.button {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 331px;
  width: 100%;
  position: relative;
  z-index: 5;
  padding: 19px;
  color: #FFFFFF;
  text-decoration: none;
  font-size: 20px;
  font-weight: 500;
  border-radius: 10px;
    background: linear-gradient(90deg, #FF5637, #F7084C, #A90A3D, #FF5637);
  background-size: 300% 100%;
  transition: 0.4s all cubic-bezier(0.25, 0.46, 0.45, 0.94);
  @include media("<=1280px") {
    
  }
  @include media("<=1080px") {
    
  }
  @include media("<=810px") {
    
  }
  @include media("<=430px") {
    max-width: 300px;
    padding: 17px;
  }
  &:hover {
      transition: all 0.3s ease-in-out;
      background-position: 100% 0;
  }
  &-footer {
    
        background: linear-gradient(90deg, #ff553700, #f7084c00 50%, #A90A3D, #FF5637);
      border: 1px solid #FF5637;
      background-size: 300% 100%;
  }
}

.hero__image {
  flex: 0 1 auto;
  position: absolute;
  max-width: 1366px;
  right: 0;
  bottom: -62px;
  width: 100%;
  @include media("<=1440px") {
    max-width: 1110px;
    bottom: 0;
  }
    @include media("<=1280px") {
      width: 88%;
    }
        @include media("<=1080px") {
          bottom: 20%;
        }
  @include media("<=810px") {
   display: none;
  }
    @include media("<=430px") {
      
    }
  img {
    width: 100%;
    display: block;
  }
}


.hero__info {
  padding-top: 160px;
  display: flex;
  align-items: center;
  position: relative;
  column-gap: 20px;
  @include media("<=1440px") {
    padding-top: 140px;
  }
    @include media("<=1280px") {
      padding-top: 0;
    }
        @include media("<=810px") {
          flex-direction: column;
          max-width: 331px;
          margin: 0 auto;
          row-gap: 30px;
          align-items: flex-start;
        }
}

.hero__item {
  display: flex;
  column-gap: 19px;
  align-items: center;
  flex: 0 1 100%;
  max-width: 500px;
  @include media("<=1440px") {
    flex: 0 1 30%;
  }
}

.hero__icon {
 
}

.hero__item-link, .hero__item-text {
  color: #fff;
  text-decoration: none;
}


