.header {
  position: relative;
  width: 100%;
  z-index: 100;
  padding: 60px 0;
  @include media("<=1280px") {
    padding: 40px 0;
  }
    @include media("<=1080px") {
      padding: 32px 0;
    }
  @include media("<=810px") {
    
  }
  @include media("<=430px") {
    padding: 20px 0;
  }
  
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__menu {
  width: 100%;
  @include media("<=810px") {
    position: fixed;
      width: 100%;
      height: 60%;
      top: 0;
      left: -130%;
      transition: all 0.3s ease-in-out;
      background-color: #030D19;
      padding: 6.25rem 0.9375rem 1.25rem 0.9375rem;
      overflow: auto;
      box-shadow: 0 15px 45px #F7084C;
  }
}

.header__nav {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  column-gap: 82px;
  @include media("<=1080px") {
    column-gap: 40px;
  }
  @include media("<=810px") {
    flex-direction: column;
    max-width: none;
    row-gap: 30px;
  }
  .button {
    @include media("<=1080px") {
      display: none;
    }
        @include media("<=810px") {
          display: flex;
        }
  }
}

.header__link {
  font-size: 24px;
  font-weight: 500;
  text-decoration: none;
  color: #ffffff;
  transition: all 0.3s linear;
  position: relative;
  @include media("<=1080px") {
    font-size: 22px;
  }
  @include media("<=810px") {
    font-size: 20px;
    background-color: transparent;
  }
  @include media("<=430px") {
    font-size: 18px;
  }
  &:hover {
    color: #F7084C;
  }
}

.header__link::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.1em;
  background: #ffffff;
  bottom: -6px;
  left: 0;
  transform: scale(0, 1);
  transition: transform 0.3s ease;
}

.header__link:hover::after {
  transform: scale(1, 1);
}

.header__logo {
  color: #ffffff;
  text-decoration: none;
  font-size: 48px;
  font-weight: 500;
  @include media("<=810px") {
    font-size: 42px;
  }
    @include media("<=430px") {
      font-size: 28px;
    }
}

