.features {

  overflow: hidden;
  padding: 107px 0 117px;
  background-image: url(../images/features-bcg.png);
  background-position: -50% -50%;
  background-repeat: no-repeat;
  @include media("<=1280px") {
    background-position: 0 -40%;
    background-repeat: no-repeat;
    background-size: cover;
  }
  @include media("<=1080px") {
    padding: 60px 0;
  }
  @include media("<=810px") {
    background-position-x: 100%;
  }
  @include media("<=430px") {
    
  }
}

.features__inner {
  display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    column-gap: 30px;
    row-gap: 60px;
    padding-top: 62px;
    @include media("<=1080px") {
        row-gap: 40px;
      }
    
      @include media("<=1000px") {
        flex-direction: column;
        
      }
    
      @include media("<=430px") {
        
      }
}

.features__item {
  display: flex;
  align-items: flex-start;
  position: relative;
  width: 100%;
  column-gap: 71px;
  flex: 0 1 44%;
  @include media("<=1080px") {
    flex: 0 1 48%;
  }
@include media("<=1000px") {
    flex: 0 1 100%;
    column-gap: 50px;
  }

  @include media("<=430px") {
    column-gap: 20px;
  }
}

.features__icon {
  max-width: 115px;
  width: 100%;
  aspect-ratio: 1 / 1;
  display: flex;
  align-items: center;
  justify-content: center;

  position: relative;
  @include media("<=430px") {
      max-width: 60px;
    }
  img {
    width: 70px;
    display: block;
    position: relative;
    @include media("<=430px") {
        width: 30px;
      }
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: #A90A3D;
    filter: blur(25px);
  }
}

.features__title {
  font-size: 40px;
    font-weight: 700;
    font-family: "Hind", sans-serif;
    margin-bottom: 31px;
    @include media("<=1280px") {
        font-size: 32px;
      }
    
      @include media("<=430px") {
        font-size: 24px;
        margin-bottom: 20px;
      }
}

.features__text {
  color: rgba(255, 255, 255, 0.80);
    font-size: 24px;
    font-weight: 500;
    line-height: 40px;
@include media("<=1280px") {
    font-size: 20px;
    line-height: 1.2;
  }

  @include media("<=430px") {
    font-size: 18px;
  }
}