

.video {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 91px 0;
  position: relative;
    z-index: 3;
  overflow: hidden;
  @include media("<=1280px") {
    padding: 80px 0;
  }
    @include media("<=810px") {
      padding: 80px 0;
    }
  @include media("<=430px") {
    padding: 60px 0 40px;
  }

}

.gallery__video {
  margin: 40px auto;
  width: 100%;
    max-width: 1320px;
    aspect-ratio: 1320 / 601;
    border-radius: 20px;
    box-shadow: 0 0 55px #F7084C;
    position: relative;
    @include media("<=430px") {
      aspect-ratio: 1 / 1;
    }
}

video {
 // height: 100%;
  width: 100%;
  max-width: 1320px;
  aspect-ratio: 1320 / 601;
  object-position: right;
  position: relative;
  border-radius: 20px;
    background:  #030D19;
  @include media("<=810px") {
    
  }

    @include media("<=430px") {
      aspect-ratio: 1 / 1;
    }
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
   
  }
}

.play-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  max-width: 73px;
  width: 100%;
  aspect-ratio: 1 / 1;
  border-radius: 50%;
  background: linear-gradient(90deg, #FF5637 -18.18%, #F7084C 100%);
  transition: all 0.3s linear;
  z-index: 10;

  img {
    width: 100%;
  }

  &:hover {
    filter: drop-shadow(0 0 15px #ff0095);
  }

  @include media("<=1080px") {
    width: 14vw;
  }

  @include media("<=430px") {
    width: 16vw;
  }
}

.play-arrow {
      position: relative;
      left: 2px;
      display: inline-block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 15px 0 15px 22px;
      border-color: transparent transparent transparent #000;
    
}

.video__title {
  color:  #030D19;
    text-shadow:
        -1px -1px 0 #fff,
        1px -1px 0 #fff,
        -1px 1px 0 #fff,
        1px 1px 0 #fff;
    font-size: 75px;
    font-style: normal;
    font-weight: 900;
    line-height: 100px;
    position: absolute;
    top: 7%;
    left: 2%;
    @include media("<=1280px") {
      font-size: 54px;
    }
        @include media("<=810px") {
          font-size: 32px;
        }
        @include media("<=600px") {
          text-align: center;
          font-size: 28px;
          max-width: 300px;
          line-height: 1.3;
          left: 1%;
        }
}

